.extend-link{
  text-decoration: none;
  cursor: pointer;
}

.extend-link:hover{
  color: #0056b3;
  cursor: pointer;
  text-decoration: none;
}
.clearfix {
  zoom: 1;
  &:before,
  &:after {
    content: "";
    display: table;
  }
  &:after {
    clear: both;
    visibility: hidden;
    font-size: 0;
    height: 0;
  }
}

